@import url("https://fonts.googleapis.com/css2?family=Chivo&display=swap");

* {
  box-sizing: border-box;
}

.icons2 {
  display: none;
}

body {
  margin: 0;
  font-family: "Chivo", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #121725;
}

.App {
  width: 1275px;
  height: 640px;
  background: url(../src/assets/image-host.jpg);
  background-position: right 75%;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.logo {
  z-index: 1;
  position: relative;
  left: 140px;
  top: -20px;
}

.pod-request {
  width: 723px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 30px;
  background-color: #121725;
  margin-top: 171px;
  padding: 10px;
}

h1 {
  font-style: normal;
  font-weight: 400;
  font-size: 52px;
  line-height: 62px;
  text-transform: uppercase;
  color: #54e6af;
  margin-top: 40px;
  margin-bottom: -10px;
}

h1 > span {
  color: white;
}

p {
  width: 445px;
  height: 84px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  color: #c2cbe5;
}

form {
  display: flex;
  flex-direction: column;
}

.form-inputs {
  display: flex;
  flex-direction: row;
}

.form-email {
  background: #2c344b;
  border-radius: 28px;
  width: 427px;
  height: 56px;
  font-family: "Chivo";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 28px;
  border: none;
  color: #ffffff;
  text-indent: 20px;
}

::placeholder {
  font-family: "Chivo";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 28px;
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.5;
}

.submit-btn {
  width: 162px;
  height: 46px;
  background: #54e6af;
  box-shadow: 0px 2px 4px rgba(84, 230, 175, 0.14);
  border-radius: 28px;
  border: none;
  font-style: bold;
  font-weight: 700;
  font-size: 14px;
  line-height: 28px;

  color: #121725;
  z-index: 1;
  margin-left: -165px;
  margin-top: 5px;
}

.submit-btn:hover {
  background: #b3ffe2;
  cursor: pointer;
  box-shadow: 0px 25px 20px -20px rgba(84, 230, 175, 0.497913);
}

.danger {
  margin-top: 10px;
  margin-left: 32px;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #fb3e3e;
}

.icons {
  display: flex;
  flex-direction: row;
  gap: 40px;
  height: 29px;
}

.dots-background {
  z-index: 1;
  position: relative;
  top: 600px;
  left: 100px;
}

@media screen and (max-width: 768px) {
  .App {
    background-position: bottom right -300px;
    max-width: 768px;
    margin-top: -120px;
  }
  .pod-request {
    position: relative;
    right: 100px;
  }

  .logo {
    left: 20px;
  }
  .dots-background {
    top: 750px;
    bottom: 0px;
    left: -700px;
    right: 0px;
  }

  * {
    margin: 0px;
  }
}

@media screen and (max-width: 375px) {
  body {
    all: revert;
  }

  .App,
  .pod-request,
  .form-inputs,
  h1,
  .icons,
  .logo {
    all: revert;
  }

  .dots-background {
    display: none;
  }

  .form-inputs {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  p {
    width: 327px;
    height: 100px;
    font-family: "Chivo";
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    color: #c2cbe5;
  }

  .form-email {
    width: 327px;
    height: 46px;
  }

  .submit-btn {
    z-index: 0;
    margin-left: 0px;
    margin-top: 0px;
    width: 327px;
    height: 46px;
  }

  body {
    height: 667px;
    max-width: 375px;
    margin: 0px;
    background: url(../src/assets/image-host.jpg);

    background-repeat: no-repeat;
    background-position: -275px;
    background-size: 650px 667px;
    mix-blend-mode: hard-light;
  }

  .App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #121725;
    max-width: 375px;
    height: 667px;
  }

  .pod-request {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }

  h1 {
    width: 327px;
    height: 76px;
    font-family: "Chivo";
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #54e6af;
  }

  .icons {
    display: none;
  }

  .icons2 {
    width: 320px;
    height: 17px;
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }

  .icons2 img {
    width: 70px;
    height: 17px;
  }

  .logo {
    margin-top: 40px;
    margin-bottom: 40px;
  }
}
